import {
  init,
  classModule,
  propsModule,
  styleModule,
  eventListenersModule,
  h,
} from "snabbdom"

// 2. patch 
const patch = init([
  styleModule,eventListenersModule
])

// 3.
let VNode = h('div#box', {
  style: {
    backgroundColor: 'red',
    height: '200px',
    width: '200px'
  }
},[
  h('h1#title', {
    style: {
      color: 'yellow'
    },
    on: {
      click () {
        console.log(123);
      }
    }
  }, 'h1h1'),
  h('p','')
])

const dom = document.querySelector('#app')
patch(dom,VNode)